<template>
  <div class="verMarcas">
    <SideNav />
    <div class="container-left">
      <md-content>
        <h2 class="title-view">Usuarios</h2>
        <md-divider></md-divider>
        <div class="md-layout md-gutter md-alignment-center">
          <md-progress-spinner
            :md-diameter="30"
            :md-stroke="3"
            md-mode="indeterminate"
            v-if="sending"
          ></md-progress-spinner>
        </div>
        <div class></div>
        <div class="md-layout md-gutter">
          <div
            class="md-layout-item md-xsmall-size-100 md-small-size-100 md-large-size-100 md-size-100"
          >
            <div>
              <md-table
                v-model="searched"
                md-sort="name"
                md-sort-order="asc"
                md-card
                md-fixed-header
                class="table-registros"
              >
                <md-table-toolbar>
                  <md-field
                    md-clearable
                    class="md-toolbar-section-rigth md-layout-item md-size-60 input-buscarRegistros"
                  >
                    <md-input
                      class="md-size-10"
                      placeholder="Buscar por nombre..."
                      v-model="search"
                      @input="searchOnTable"
                    />
                  </md-field>
                </md-table-toolbar>

                <md-table-empty-state
                  md-label="Sin resultados"
                  :md-description="
                    `No se encontro el registro '${search}'. Itenta con otro nombre o registra uno nuevo`
                  "
                >
                  <router-link to="/addUsuario">
                    <md-button class="md-primary md-raised"
                      >Agregar</md-button
                    >
                  </router-link>
                </md-table-empty-state>

                <md-table-row slot="md-table-row" slot-scope="{ item }" class>
                  <md-table-cell md-label="Nombre" md-sort-by="nombre">{{
                    item.nombre
                  }}</md-table-cell>
                  <md-table-cell md-label="Correo" md-sort-by="correo">{{
                    item.correo
                  }}</md-table-cell>
                  <md-table-cell md-label="Opciones" class="cell-opciones">
                    <md-button
                      class="btnEdit"
                      @click="showDialogDelete(item.id)"
                    >
                      <md-icon>delete</md-icon>
                    </md-button>
                    <router-link :to="'/editUsuario/' + item.id">
                      <md-icon @click="active = true">edit</md-icon>
                    </router-link>
                  </md-table-cell>
                </md-table-row>
              </md-table>
            </div>
          </div>
          <div
            class="md-layout-item md-xsmall-size-100 md-small-size-40 md-size-50"
          ></div>
          <div class="md-layout-item"></div>
        </div>

        <div>
          <md-dialog-confirm
            :md-active.sync="active"
            md-title="Alerta"
            md-content="Desea eliminar este registro?"
            md-confirm-text="Aceptar"
            md-cancel-text="Cancelar"
            @md-cancel="onCancel"
            @md-confirm="onConfirm"
          />

          <span v-if="value">Value: {{ value }}</span>
        </div>
      </md-content>
    </div>
    <md-snackbar :md-active.sync="showSnackbar">{{ alertMessage }}</md-snackbar>
  </div>
</template>

<script>
const toLower = (text) => {
  return text.toString().toLowerCase();
};

const searchByName = (items, term) => {
  if (term) {
    return items.filter((item) => toLower(item.nombre).includes(toLower(term)));
  }

  return items;
};

import SideNav from "@/components/SideNav.vue";

// @ is an alias to /src
export default {
  name: "VerRegiones",
  components: {
    SideNav,
  },
  data: () => ({
    active: false,
    value: null,
    sending: true,
    id_marca: 0,
    active: false,
    value: null,
    alertMessage: "",
    showSnackbar: false,
    search: null,
    searched: [],
    users: [],
    unidades: [],
    idDelete: null,
  }),
  mounted() {
    this.obtenerMarcas();
  },
  methods: {
    onConfirm() {
      var context = this;
      let formData = new FormData();
      formData.append("id", this.idDelete);
      formData.append("token", localStorage.getItem("tokenUser_et"));
      this.sedingInfo = true;
      const baseURI = this.$baseURL + "menudigital/index.php/usuario/delete";
      this.$http
        .post(
          baseURI,
             formData,
          {
          headers: {
            "Content-Type": "multipart/form-data",
          }
          }
        )
        .then(res => {
          console.log(res);
          if (res.data.status != null) {
            if (res.data.status.toUpperCase() === "OK") {
              this.obtenerMarcas();
            }

            if (res.data.status.toUpperCase() === "ERROR") {
              this.alertMessage = res.data.respuesta;
              this.showSnackbar = true;
              this.sending = false;
            }

            if (
              res.data.status.toUpperCase() != "OK" &&
              res.data.status.toUpperCase() != "ERROR"
            ) {
              this.alertMessage =
                "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
              this.showSnackbar = true;
              this.sending = false;
            }
          } else {
            this.alertMessage =
                "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
              this.showSnackbar = true;
              this.sedingInfo = false;
          }
          //
        })
        .catch(function(error) {
          // handle error
          console.log(error);
          context.alertMessage =
            "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
          context.showSnackbar = true;
          context.sending = false;
        });
    },
    onCancel() {},
    searchOnTable() {
      //alert(this.search);
      //console.log(this.users);
      //this.searched = this.users//;
      this.searched = searchByName(this.users, this.search);
      //
      //this.searched = this.filteredClients;
      console.log(this.searched);
    },
    obtenerMarcas() {
      this.users = [];
      

      const baseURI = this.$baseURL + "menudigital/index.php/usuario";
      this.$http
        .get(
          baseURI,
          { params: { id_marca: this.id_marca, token: localStorage.getItem('tokenUser_et') } },
          { "Access-Control-Allow-Origin": "*" }
        )
        .then((res) => {
          console.log(res.data.registros);
          if (res.data.status === "OK") {
            res.data.registros.map((item) => {
              const obj = {
                id: item.id,
                nombre: item.nombre,
                correo: item.correo
              };
              this.users.push(obj);
              this.sending = false;
            });
          }

          if (res.data.status.toUpperCase() === "ERROR") {
            this.alertMessage = res.data.respuesta;
            this.showSnackbar = true;
            this.sending = false;
            this.$router.push("/");
          }
          this.searched = this.users;
        });
    },
    showDialogDelete(id) {
    this.active = true;
    this.idDelete = id;
  },
  },
};
</script>

