<script>
import { validationMixin } from "vuelidate";
import {
  required,
  email,
  minLength,
  maxLength,
} from "vuelidate/lib/validators";

import SideNav from "@/components/SideNav.vue";

// @ is an alias to /src
export default {
  name: "EditarExtra",
  components: {
    SideNav,
  },
  props: {
    info: String,
  },
  validations: {
    form: {
      extra: {
        required,
      },
      descripcion: {
        required,
      },
    },
  },
  data() {
    return {
      boolean: false,
      users: [],
      sedingInfo: true,
      form: {
        id: null,
        extra: null,
        descripcion: null,
        id_marca: null,
        id_rol: null,
        activo: null,
        extra: null,
        precio_nacional: null,
        precio_cc: null,
        precio_tijuana: null,
        precio_pl: null,
        precio_acapulco: null,
        precio_aeropuerto: null,
      },
      sedingInfo: true,
      alertMessage: "",
      showSnackbar: false,
    };
  },
  mounted() {

    if (localStorage.getItem("tokenUser_et") === "") {
      this.$router.push("/");
    } 

    const baseURI =this.$baseURL + "menudigital/index.php/extra/update";
    this.$http
      .get(
        baseURI,
        { params: { id: this.$route.params.id, token: localStorage.getItem("tokenUser_et") } },
        { "Access-Control-Allow-Origin": "*" }
      )
      .then((res) => {
        //this.users = result.data;
        console.log(res);
        if (res.data.status != null) {
          if (res.data.status === "OK") {
            //mostrarSucursales
            this.form.id = res.data.registro.id;
            this.form.extra = res.data.registro.nombre;
            this.form.descripcion = res.data.registro.descripcion;
            this.form.precio_nacional = res.data.registro.precio_nacional;
            this.form.precio_acapulco = res.data.registro.precio_acapulco;
            this.form.precio_cc = res.data.registro.precio_cc;
            this.form.precio_tijuana = res.data.registro.precio_tijuana;
            this.form.precio_pl = res.data.registro.precio_pl;
            this.form.precio_aeropuerto = res.data.registro.precio_aeropuerto;
            this.sedingInfo = false;
            if (this.form.activo == 1) {
              this.boolean = true;
            } else {
              this.boolean = false;
            }
          }

          if (
            res.data.status.toUpperCase() != "OK" &&
            res.data.status.toUpperCase() != "ERROR"
          ) {
            this.alertMessage =
              "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
            this.showSnackbar = true;
            this.sending = false;
          }
          if (
            res.data.status.toUpperCase() != "OK" &&
            res.data.status.toUpperCase() != "ERROR"
          ) {
            this.alertMessage =
              "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
            this.showSnackbar = true;
            this.sending = false;
          }

          if (res.data.status.toUpperCase() === "ERROR") {
            this.alertMessage = res.data.respuesta;
            this.showSnackbar = true;
            this.sedingInfo = false;
            this.$router.push("/");
          }
        } else {
          this.alertMessage =
            "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
          this.showSnackbar = true;
          this.sedingInfo = false;
        }
      });
  },
  methods: {
    getValidationClass(fieldName) {
      const field = this.$v.form[fieldName];

      if (field) {
        return {
          "md-invalid": field.$invalid && field.$dirty,
        };
      }
    },
    clearForm() {
      this.$v.$reset();
    },
    saveExtras() {
      this.sedingInfo = true;
      if(this.boolean){
        this.form.activo = 1;
      }else{
        this.form.activo = 0;
        }
      var a = this;
      
      let formData = new FormData();
      formData.append("token",localStorage.getItem("tokenUser_et"));
      formData.append("id",this.form.id);
      formData.append("nombre", this.form.extra);
      formData.append("precio_nacional", this.form.precio_nacional);
      formData.append("precio_acapulco", this.form.precio_acapulco);
      formData.append("precio_cc", this.form.precio_cc);
      formData.append("precio_tijuana", this.form.precio_tijuana);
      formData.append("precio_pl", this.form.precio_pl);
      formData.append("precio_aeropuerto", this.form.precio_aeropuerto);
      formData.append("descripcion", this.form.descripcion);

      const baseURI = this.$baseURL + "menudigital/index.php/extra/update_db";
      this.$http
        .post(
          baseURI,
          formData,
          { "Access-Control-Allow-Origin": "*" }
        )
        .then((res) => {
          console.log(res);
          if (res.data.status != null) {
            if (res.data.status.toUpperCase() === "OK") {
              this.$router.push("/verExtras");
            }

            if (res.data.status.toUpperCase() === "ERROR") {
              this.alertMessage = res.data.respuesta;
              this.showSnackbar = true;
              this.sending = false;
            }

            if (
              res.data.status.toUpperCase() != "OK" &&
              res.data.status.toUpperCase() != "ERROR"
            ) {
              this.alertMessage =
                "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
              this.showSnackbar = true;
              this.sending = false;
            }
          } else {
            this.alertMessage =
              "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
            this.showSnackbar = true;
            this.sedingInfo = false;
          }
          //
        })
        .catch(function(error) {
          // handle error
          console.log(error);
          a.alertMessage =
            "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
          a.showSnackbar = true;
          a.sedingInfo = false;
        });
    },
    validateExtra() {
      this.$v.$touch();

      if (!this.$v.$invalid) {
        this.saveExtras();
      }
    },
  },
};
</script>

