<script>
export default {
  name: "SideNav",
  created() {
    this.nameUser = localStorage.getItem("nombreUser_et");
  },
  data() {
    return {
      expandNews: false,
      expandSingle: false,
      clickMenu: false,
      clickChildSide: false,
      nameUser: null,
      selectMarca: [],
      marca: null,
      showSelectMarca: false,
    };
  },
  mounted() {
    //alert(localStorage.getItem("id_rol_et"));
    if(localStorage.getItem("id_rol_et") === "1"){
      this.showSelectMarca = true;
    }
    const baseURI = this.$baseURL + "menudigital/index.php/marca";
    this.$http
      .get(
        baseURI,
        {
          params: {
            token: localStorage.getItem("tokenUser_et"),
          },
        },
        { "Access-Control-Allow-Origin": "*" }
      )
      .then((res) => {
        console.log(res.data.registros);
        if (res.data.status === "OK") {
          res.data.registros.map((item) => {
            const obj = {
              id: item.id,
              nombre: item.nombre,
            };
            this.selectMarca.push(obj);
          });

          this.marca = localStorage.getItem("id_marca_et");
        }

        if (res.data.status.toUpperCase() === "ERROR") {
          this.alertMessage = res.data.respuesta;
          this.showSnackbar = true;
          this.sending = false;
          //this.$router.push("/");
        }
      });
  },
  methods: {
    selectOption(v){
      //alert(localStorage.getItem("id_marca_et") + " --- "   + v);
      
      if(localStorage.getItem("id_marca_et") !== v){
        localStorage.setItem("id_marca_et", v);
        this.$router.go(0);
      }
      //this.$router.go(0);
    },
    onClickMenu() {
      this.clickMenu = true;
    },
    onClickSide(event) {
      if (this.clickChildSide) {
        //alert(event.currentTarget.id);
      } else {
        //alert("solo side");
        this.clickMenu = false;
      }
      this.clickChildSide = false;
    },
    onClickSide2(event) {
      this.clickChildSide = true;
      //alert(event.currentTarget.id + " - " + this.clickChildSide);
    },
    logOut() {
      //document.cookie = "tokenUser_et=cookievalue; expires= Thu, 21 Aug 2014 20:00:00 UTC"
      localStorage.removeItem("tokenUser_et");
      this.$router.push("/");
    },
  },
};
</script>

