<template>
  <div class="addRegion">
    <SideNav />
    <div class="container-left">
      <md-content>
        <h2 class="title-view">Editar Region</h2>
        <md-divider></md-divider>
        <div class="md-layout md-gutter">
          <div
            class="md-layout-item md-xsmall-size-100 md-small-size-60 md-size-50"
          >
            <div>
              <form novalidate class @submit.prevent="validateRegion">
                <div class="md-layout md-gutter md-alignment-center">
                  <div class="md-layout-item md-size-100">
                    <md-field :class="getValidationClass('region')">
                      <label for="region">Nombre</label>
                      <md-input
                        name="region"
                        id="region"
                        autocomplete="text"
                        type="text"
                        v-model="form.region"
                      />
                      <span class="md-error">El campo no puede ir vacio</span>
                    </md-field>
                  </div>
                </div>

                <div class="md-layout md-gutter md-alignment-center">
                  <div class="md-layout-item md-size-100">
                    <md-field :class="getValidationClass('descripcion')">
                      <label for="descripcion">Descripcin</label>
                      <md-input
                        name="descripcion"
                        id="descripcion"
                        autocomplete="text"
                        type="text"
                        v-model="form.descripcion"
                      />
                      <span class="md-error">El campo no puede ir vacio</span>
                    </md-field>
                  </div>
                </div>

                <div class="md-layout md-gutter">
                  <div class="md-layout-item md-alignment-left">
                    <md-checkbox class="chk-activo" v-model="boolean"
                      >Activo</md-checkbox
                    >
                  </div>
                </div>

                <div class="md-layout md-gutter md-alignment-center">
                  <md-progress-spinner
                    :md-diameter="30"
                    :md-stroke="3"
                    md-mode="indeterminate"
                    v-if="sedingInfo"
                  ></md-progress-spinner>
                </div>

                <md-card-actions class="md-layout md-gutter md-alignment-right">
                  <md-button
                    type="submit"
                    class="md-raised md-accent btnEntrar"
                    :disabled="sedingInfo"
                    >Guardar</md-button
                  >
                </md-card-actions>
                <md-snackbar :md-active.sync="showSnackbar">{{
                  alertMessage
                }}</md-snackbar>
              </form>
            </div>
          </div>
          <div
            class="md-layout-item md-xsmall-size-100 md-small-size-40  md-size-50 "
          >
          </div>
          <div class="md-layout-item"></div>
        </div>
      </md-content>
    </div>
  </div>
</template>

<script>
import { validationMixin } from "vuelidate";
import {
  required,
  email,
  minLength,
  maxLength,
} from "vuelidate/lib/validators";

import SideNav from "@/components/SideNav.vue";

// @ is an alias to /src
export default {
  name: "EditarRegion",
  components: {
    SideNav,
  },
  props: {
    info: String,
  },
  validations: {
    form: {
      region: {
        required,
      },
      descripcion: {
        required,
      },
    },
  },
  data() {
    return {
      boolean: false,
      users: [],
      sedingInfo: true,
      form: {
        id: null,
        region: null,
        descripcion: null,
        id_marca: null,
        id_rol: null,
        activo: null
      },
      sedingInfo: true,
      alertMessage: "",
      showSnackbar: false,
    };
  },
  mounted() {
    if (localStorage.getItem("tokenUser_et") === "") {
      this.$router.push("/");
    } 

    const baseURI =this.$baseURL + "menudigital/index.php/region/update";
    this.$http
      .get(
        baseURI,
        { params: { id: this.$route.params.id,id_marca:localStorage.getItem("id_marca_et"), token: localStorage.getItem("tokenUser_et") } },
        { "Access-Control-Allow-Origin": "*" }
      )
      .then((res) => {
        //this.users = result.data;
        console.log(res);
        if (res.data.status != null) {
          if (res.data.status === "OK") {
            //mostrarSucursales
            this.form.id = res.data.registro.id;
            this.form.region = res.data.registro.nombre;
            this.form.descripcion = res.data.registro.descripcion;
            this.form.activo = res.data.registro.activo;
            this.sedingInfo = false;
            if (this.form.activo == 1) {
              this.boolean = true;
            } else {
              this.boolean = false;
            }
          }

          if (
            res.data.status.toUpperCase() != "OK" &&
            res.data.status.toUpperCase() != "ERROR"
          ) {
            this.alertMessage =
              "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
            this.showSnackbar = true;
            this.sending = false;
          }
          if (
            res.data.status.toUpperCase() != "OK" &&
            res.data.status.toUpperCase() != "ERROR"
          ) {
            this.alertMessage =
              "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
            this.showSnackbar = true;
            this.sending = false;
          }

          if (res.data.status.toUpperCase() === "ERROR") {
            this.alertMessage = res.data.respuesta;
            this.showSnackbar = true;
            this.sedingInfo = false;
            this.$router.push("/");
          }
        } else {
          this.alertMessage =
            "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
          this.showSnackbar = true;
          this.sedingInfo = false;
        }
      });
  },
  methods: {
    getValidationClass(fieldName) {
      const field = this.$v.form[fieldName];

      if (field) {
        return {
          "md-invalid": field.$invalid && field.$dirty,
        };
      }
    },
    clearForm() {
      this.$v.$reset();
    },
    saveRegion() {
      this.sedingInfo = true;
      if(this.boolean){
        this.form.activo = 1;
      }else{
        this.form.activo = 0;
        }
      var a = this;
      const baseURI = this.$baseURL + "menudigital/index.php/region/update_db";
      this.$http
        .post(
          baseURI,
          {
            id: this.form.id,
            token: localStorage.getItem("tokenUser_et"),
            nombre: this.form.region,
            descripcion: this.form.descripcion,
            id_marca: localStorage.getItem("id_marca_et"),
            id_region: this.form.id_region,
            activo: this.form.activo
          },
          { "Access-Control-Allow-Origin": "*" }
        )
        .then((res) => {
          console.log(res);
          if (res.data.status != null) {
            if (res.data.status.toUpperCase() === "OK") {
              this.$router.push("/verRegiones");
            }

            if (res.data.status.toUpperCase() === "ERROR") {
              this.alertMessage = res.data.respuesta;
              this.showSnackbar = true;
              this.sending = false;
            }

            if (
              res.data.status.toUpperCase() != "OK" &&
              res.data.status.toUpperCase() != "ERROR"
            ) {
              this.alertMessage =
                "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
              this.showSnackbar = true;
              this.sending = false;
            }
          } else {
            this.alertMessage =
              "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
            this.showSnackbar = true;
            this.sedingInfo = false;
          }
          //
        })
        .catch(function(error) {
          // handle error
          console.log(error);
          a.alertMessage =
            "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
          a.showSnackbar = true;
          a.sedingInfo = false;
        });
    },
    validateRegion() {
      this.$v.$touch();

      if (!this.$v.$invalid) {
        this.saveRegion();
      }
    },
  },
};
</script>

