<template>
  <div class="addMarca">
    <SideNav />
    <div class="container-left">
      <md-content>
        <h2 class="title-view">Agregar Marca</h2>
        <md-divider></md-divider>
        <div class="md-layout md-gutter">
          <div
            class="md-layout-item md-xsmall-size-100 md-small-size-60 md-size-50"
          >
            <div>
              <form novalidate class @submit.prevent="validateMarca">
                <div class="md-layout md-gutter md-alignment-center">
                  <div class="md-layout-item md-size-100">
                    <md-field :class="getValidationClass('marca')">
                      <label for="marca">Nombre</label>
                      <md-input
                        name="marca"
                        id="marca"
                        autocomplete="text"
                        type="text"
                        v-model="form.marca"
                      />
                      <span class="md-error">El campo no puede ir vacio</span>
                    </md-field>
                  </div>
                </div>

                <md-field>
                  <label>Logo de marca</label>
                  <md-file
                    v-model="imgMarca"
                    @change="getFile"
                    accept="image/*"
                  />
                </md-field>

                <div class="md-layout md-gutter md-alignment-center">
                  <md-progress-spinner
                    :md-diameter="30"
                    :md-stroke="3"
                    md-mode="indeterminate"
                    v-if="sedingInfo"
                  ></md-progress-spinner>
                </div>

                <md-card-actions class="md-layout md-gutter md-alignment-right">
                  <md-button
                    type="submit"
                    class="md-raised md-accent btnEntrar"
                    :disabled="sedingInfo"
                    >Guardar</md-button
                  >
                </md-card-actions>
                <md-snackbar :md-active.sync="showSnackbar">{{
                  alertMessage
                }}</md-snackbar>
              </form>
            </div>
          </div>
          <div
            class="md-layout-item md-xsmall-size-100 md-small-size-40  md-size-50 "
          >
          
          </div>
          <div class="md-layout-item"></div>
        </div>
      </md-content>
    </div>
  </div>
</template>

<script>
import { validationMixin } from "vuelidate";
import {
  required,
  email,
  minLength,
  maxLength,
} from "vuelidate/lib/validators";

import SideNav from "@/components/SideNav.vue";

// @ is an alias to /src
export default {
  name: "AgregarMarca",
  components: {
    SideNav,
  },
  validations: {
    form: {
      marca: {
        required,
      },
    },
  },
  data() {
    return {
      users: [],
      form: {
        marca: null,
        id_marca: null,
        id_rol: null,
      },
      sedingInfo: false,
      alertMessage: "",
      showSnackbar: false,
      imgMarca: null,
      file: null,
    };
  },
  created() {
    

    if (localStorage.getItem("tokenUser_et") === "") {
      this.$router.push("/");
    }
  },
  methods: {
    getValidationClass(fieldName) {
      const field = this.$v.form[fieldName];

      if (field) {
        return {
          "md-invalid": field.$invalid && field.$dirty,
        };
      }
    },
    clearForm() {
      this.$v.$reset();
    },
    saveMarca() {
      this.sedingInfo = true;
      let formData = new FormData();
      formData.append("file", this.file);
      formData.append("token", localStorage.getItem("tokenUser_et"));
      formData.append("nombre", this.form.marca);
      //alert(this.form.unidad);
      var a = this;
      const baseURI = this.$baseURL + "menudigital/index.php/marca/add_db";
      this.$http
        .post(baseURI, formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((res) => {
          console.log(res);
          if (res.data.status != null) {
            if (res.data.status.toUpperCase() === "OK") {
              this.$router.push("/verMarcas");
            }

            if (res.data.status.toUpperCase() === "ERROR") {
              this.alertMessage = res.data.respuesta;
              this.showSnackbar = true;
              this.sedingInfo = false;
            }

            if (
              res.data.status.toUpperCase() != "OK" &&
              res.data.status.toUpperCase() != "ERROR"
            ) {
              this.alertMessage =
                "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
              this.showSnackbar = true;
              this.sedingInfo = false;
            }
          } else {
            this.alertMessage =
              "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
            this.showSnackbar = true;
            this.sedingInfo = false;
          }
          //
        })
        .catch(function(error) {
          // handle error
          //console.log(error);
          a.alertMessage =
            "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
          a.showSnackbar = true;
          a.sedingInfo = false;
        });
    },
    validateMarca() {
      this.$v.$touch();

      if (!this.$v.$invalid) {
        this.saveMarca();
      }
    },
    getFile() {
      this.file = this.imgMarca.srcElement.files[0];
    },
  },
};
</script>

