<script>
import { validationMixin } from "vuelidate";
import {
  required,
  email,
  minLength,
  maxLength,
} from "vuelidate/lib/validators";

import SideNav from "@/components/SideNav.vue";

// @ is an alias to /src
export default {
  name: "AgregarRegion",
  components: {
    SideNav,
  },
  props: {
    info: String,
  },
  validations: {
    form: {
      platillo: {
        required,
      },
      id_categoria: {
        required,
      },
      /*/*id_sucursal: {
        required,
      },*/
      precio_nacional: {
        required,
      },
      precio_cc: {
        required,
      },
      precio_tijuana: {
        required,
      },
      precio_pl: {
        required,
      },
      precio_acapulco: {
        required,
      },
    },
  },
  data() {
    return {
      users: [],
      experiencias: [],
      experiencias2: [],
      categorias: [],
      categorias2: [],
      extras: [],
      /*extras2: [],*/
      subcategorias: [],
      sucursales: [],
      medidasXProd: [
        {
          numero: 1,
          nombre_medida: "",
          cantidad_x_porcion_medida: "",
          precio_nacional: 0,
          precio_acapulco: 0,
          precio_cc: 0,
          precio_tijuana: 0,
          precio_pl: 0,
          precio_aeropuerto: 0
        },
        {
          numero: 2,
          nombre_medida: "",
          cantidad_x_porcion_medida: "",
          precio_nacional: 0,
          precio_acapulco: 0,
          precio_cc: 0,
          precio_tijuana: 0,
          precio_pl: 0,
          precio_aeropuerto: 0
        },
      ],
      medidas: [],
      indicarMedidas: false,
      sedingInfo: false,
      agregarImagenSecundaria: false,
      form: {
        imgProducto: null,
        id_producto: null,
        platillo: null,
        cantidad_x_porcion: null,
        id_marca: null,
        id_categoria: null,
        id_subcategoria: "0",
        id_experiencia: null,
        id_sucursal: "",
        descripcion: "",
        simbologia: "",
        precio_nacional: null,
        precio_cc: 0,
        precio_tijuana: 0,
        precio_pl: 0,
        precio_acapulco: null,
        precio_aeropuerto: 0,
        logo: "",
        numero_orden: 0,
        descripcion_imagen: "",
      },
    };
  },
  methods: {
    openImgSecundaria() {
      this.fileImgSecundaria = this.imgSecundaria.srcElement.files[0];
    },
    eliminar(index) {
      if (this.medidasXProd.length > 2) {
        this.medidasXProd.splice(index, 1);
      } else {
        alert(
          "Si solo desea agregar una medida, desmarque la opcin de agregar medidas"
        );
      }
    },
    getValidationClass(fieldName) {
      const field = this.$v.form[fieldName];

      if (field) {
        return {
          "md-invalid": field.$invalid && field.$dirty,
        };
      }
    },
    agregarMedida() {
      const obj = {
        numero: this.medidasXProd.length + 1,
        nombre_medida: "",
        cantidad_x_porcion_medida: "",
        precio_nacional: 0,
        precio_acapulco: 0,
        precio_cc: 0,
        precio_tijuana: 0,
        precio_pl: 0,
        precio_aeropuerto: 0
      };
      this.medidasXProd.push(obj);
    },
    submitForm() {
      this.sedingInfo = true;
      var a = this;
      let formData = new FormData();
      formData.append("file", this.file);
      formData.append("detalle_imagen", this.fileImgSecundaria);
      formData.append("token", localStorage.getItem("tokenUser_et"));
      formData.append("id_marca",localStorage.getItem("id_marca_et"));
      formData.append("platillo", this.form.platillo);
      formData.append("id_categoria", this.form.id_categoria);
      formData.append("id_subcategoria", this.form.id_subcategoria);
      formData.append("id_experiencia", this.experiencias2.toString());
      //formData.append("extra", this.extras2.toString());
      formData.append("orden", this.form.numero_orden);
      formData.append("cantidad_x_porcion", this.form.cantidad_x_porcion);
      formData.append("logo", this.form.logo);
      formData.append("descripcion", this.form.descripcion);
      formData.append("simbologia", this.form.simbologia);
      //formData.append("id_sucursal", this.form.id_sucursal);
      formData.append("nacional", this.form.precio_nacional);
      formData.append("cc", this.form.precio_cc);
      formData.append("tijuana", this.form.precio_tijuana);
      formData.append("pl", this.form.precio_pl);
      formData.append("acapulco", this.form.precio_acapulco);
      formData.append("precio_aeropuerto", this.form.precio_aeropuerto);
      //formData.append("categorias", this.categorias2.toString());
      formData.append("descripcion_imagen", this.form.descripcion_imagen);

      if (this.indicarMedidas !== false) {
        formData.append("medidas", JSON.stringify(this.medidasXProd));
      } else {
        formData.append("medidas", "");
      }

      this.$http
        .post(
         this.$baseURL
          + "menudigital/index.php/articulo/add_db",
          formData,
          {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          }
        )
        .then(function(res) {
          console.log(res.data);
          if (res.data.status != null) {
            if (res.data.status.toUpperCase() === "OK") {
              a.$router.push("/verProductos");
            }

          } else {
            this.alertMessage =
              "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
            this.showSnackbar = true;
            this.sedingInfo = false;
          }
        });
    },
    open() {
      console.log(this.form.imgProducto.srcElement.files[0]);
      this.file = this.form.imgProducto.srcElement.files[0];
      this.form.imgProducto = "img";
    },
    changeMedidas() {
      if (this.indicarMedidas === false) {
        //alert();
      }
    },
    validateProducto() {
      this.$v.$touch();

      if (!this.$v.$invalid) {
        this.submitForm();
      }
    },
  },
  created() {
    if (
      localStorage.getItem("tokenUser_et") === null
    ) {
      this.$router.push("/");
    } 
    this.id_marca = localStorage.getItem("id_marca_et");
  },
  mounted() {
    const baseURI = this.$baseURL + "menudigital/index.php/articulo/add";
    this.$http
      .get(
        baseURI,
        {
          params: {
            id_marca: localStorage.getItem("id_marca_et"),
            token: localStorage.getItem("tokenUser_et"),
          },
        },
        { "Access-Control-Allow-Origin": "*" }
      )
      .then((res) => {
        if (res.data.status != null) {
          if (res.data.status === "OK") {
            this.subcategorias = res.data.subcategorias;
            this.categorias = res.data.categorias;
            this.sucursales = res.data.sucursales;
            this.experiencias = res.data.experiencias;
            //this.extras = res.data.extras;
            this.medidas = res.data.medidas;
            this.sedingInfo = false;
          }

          if (res.data.status.toUpperCase() === "ERROR") {
            this.alertMessage = res.data.respuesta;
            this.showSnackbar = true;
            this.sedingInfo = false;
            this.$router.push("/");
          }
        }
      });
  },
};
</script>

