<template>
  <div class="addCategoria">
    <SideNav />
    <div class="container-left">
      <md-content>
        <h2 class="title-view">Agregar Categoria</h2>
        <md-divider></md-divider>
        <div class="md-layout md-gutter">
          <div class="md-layout-item md-xsmall-size-100 md-small-size-60 md-size-50">
            <div>
              <form novalidate class @submit.prevent="validateRegion">
                <div class="md-layout md-gutter md-alignment-center">
                  <div class="md-layout-item md-size-100">
                    <md-field :class="getValidationClass('categoria')">
                      <label for="categoria">Nombre</label>
                      <md-input name="categoria" id="categoria" autocomplete="text" type="text" v-model="form.categoria"/>
                      <span class="md-error">El campo no puede ir vacio</span>
                    </md-field>
                  </div>
                </div>

                <div class="md-layout md-gutter md-alignment-center">
                  <div class="md-layout-item md-size-100">
                    <md-field :class="getValidationClass('descripcion')">
                      <label for="descripcion">Descripcin</label>
                      <md-textarea name="descripcion" id="descripcion" autocomplete="text" type="text" 
                      v-model="form.descripcion"/>
                      <span class="md-error">El campo no puede ir vacio</span>
                    </md-field>
                  </div>
                </div>

                <md-field :class="getValidationClass('imgCategoria')">
                  <label for="imgCategoria">Imagen de categoria</label>
                  <md-file
                    id="imgCategoria"
                    name="imgCategoria"
                    v-model="imgCategoria"
                    @change="getFile($event)"
                    accept="image/*"
                  />
                  <span class="md-error">Debes de agregar una imagen</span>
                </md-field>

                <md-field>
                  <label>Titulo en imagen de categoria</label>
                  <md-file
                    v-model="imgCategoriaTitle"
                    @change="getFileTitle"
                    accept="image/*"
                  />
                  
                </md-field>

                <div class="md-layout md-gutter md-alignment-center">
                  <md-progress-spinner
                    :md-diameter="30"
                    :md-stroke="3"
                    md-mode="indeterminate"
                    v-if="sedingInfo"
                  ></md-progress-spinner>
                </div>

                <md-card-actions class="md-layout md-gutter md-alignment-right">
                  <md-button
                    type="submit"
                    class="md-raised md-accent btnEntrar"
                    :disabled="sedingInfo"
                  >Guardar</md-button>
                </md-card-actions>
                <md-snackbar :md-active.sync="showSnackbar">{{alertMessage}}</md-snackbar>
              </form>
            </div>
          </div>
          <div class="md-layout-item md-xsmall-size-100 md-small-size-40  md-size-50 ">
            
          </div>
          <div class="md-layout-item"></div>
        </div>
      </md-content>
    </div>
  </div>
</template>

<script>
import { validationMixin } from "vuelidate";
import {
  required,
  email,
  minLength,
  maxLength
} from "vuelidate/lib/validators";

export function requiredImg(value = "") {
  alert(value);
  return !value.includes("gmail");
}

import SideNav from "@/components/SideNav.vue";
// @ is an alias to /src
export default {
  name: "AgregarCategoria",
  components: {
    SideNav
  },
  validations: {
    form: {
      categoria: {
        required
      },
      imgCategoria:{
        requiredImg
      }
    }
  },
  data() {
    return {
      users: [],
      marcas: [],
      form: {
        nombre_categoria: null,
        descripcion: "",
        titulo_publico: null,
        descripcion_platillos: null,
      },
      sedingInfo: false,
      alertMessage: "",
      showSnackbar: false,
      imgCategoria: null,
      imgCategoriaTitle: null,
      file: null,
      fileCategoriaTitle: null
    };
  },
  created() {
    if (localStorage.getItem("tokenUser_et") === "") {
      this.$router.push("/");
    } 
  },
  methods: {
    getValidationClass(fieldName) {
      const field = this.$v.form[fieldName];

      if (field) {
        return {
          "md-invalid": field.$invalid && field.$dirty
        };
      }
    },
    clearForm() {
      this.$v.$reset();
    },
    saveRegion(){
      this.sedingInfo = true;
      let formData = new FormData();
      formData.append("file", this.file);
      formData.append("imagen_titulo_categoria", this.fileCategoriaTitle);
      formData.append("token", localStorage.getItem("tokenUser_et"));
      formData.append("nombre", this.form.categoria);
      formData.append("descripcion", this.form.descripcion);
      formData.append("id_marca",localStorage.getItem("id_marca_et"));
      var a = this;
      const baseURI = this.$baseURL + "menudigital/index.php/categoria/add_db";
      this.$http
        .post(
          baseURI,
             formData,
             {headers: {
            "Content-Type": "multipart/form-data",
          }}
        )
        .then(res => {
          console.log(res);
          if (res.data.status != null) {
            if (res.data.status.toUpperCase() === "OK") {
              this.$router.push("/verCategorias");
            }

            if (res.data.status.toUpperCase() === "ERROR") {
              this.alertMessage = res.data.respuesta;
              this.showSnackbar = true;
              this.sending = false;
            }

            if (
              res.data.status.toUpperCase() != "OK" &&
              res.data.status.toUpperCase() != "ERROR"
            ) {
              this.alertMessage =
                "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
              this.showSnackbar = true;
              this.sending = false;
            }
          } else {
            this.alertMessage = "Ocurrio un error al intentar enviar la informacin del servidor, Comuniquese con soporte";
              this.showSnackbar = true;
              this.sedingInfo = false;
          }
          //
        })
        .catch(function(error) {
          // handle error
          console.log(error);
          a.alertMessage = "Ocurrio un error al intentar conectarse con el servidor, Comuniquese con soporte";
          a.showSnackbar = true;
          a.sedingInfo = false;
        });
    },
    validateRegion() {
      this.$v.$touch();

      if (!this.$v.$invalid) {
        this.saveRegion();
      }
    },
    getFile(event){
      var files = event.target.files || event.dataTransfer.files;
      if (!files.length) {
        return;
      }else{
        //alert();
      }
      this.$v.$touch();

      this.file = this.imgCategoria.srcElement.files[0];
      //this.imgCategoria = "img";
      this.imgCategoria = "imagen";
    },
    getFileTitle(){
    this.fileCategoriaTitle = this.imgCategoriaTitle.srcElement.files[0];
    
    }
  },
  mounted(){
    const baseURI = this.$baseURL + "menudigital/index.php/categoria/add";
    this.$http
      .get(
        baseURI,
        { params: { id_marca: localStorage.getItem("id_marca_et")  , token: localStorage.getItem("tokenUser_et") } },
        { "Access-Control-Allow-Origin": "*" }
      )
      .then((res) => {
        //this.users = result.data;
        console.log(res.data);
        if(res.data.status != null){
          if (res.data.status === "OK") {
          this.marcas = res.data.marcas;
          //this.sedingInfo = false;
        }

        if (res.data.status.toUpperCase() === "ERROR") {
          this.alertMessage = res.data.respuesta;
          this.showSnackbar = true;
          this.sedingInfo = false;
          this.$router.push("/");
        }
        }
        //
      });
  }
};
</script>

