<template>
  <div class="addRegion">
    <SideNav />
    <div class="container-left">
      <md-content>
        <h2 class="title-view">Editar Categoria</h2>
        <md-divider></md-divider>
        <div class="md-layout md-gutter">
          <div
            class="md-layout-item md-xsmall-size-100 md-small-size-60 md-size-50"
          >
            <div>
              <form novalidate class @submit.prevent="validateCategoria">
                <div class="md-layout md-gutter md-alignment-center">
                  <div class="md-layout-item md-size-100">
                    <md-field :class="getValidationClass('categoria')">
                      <label for="categoria">Nombre</label>
                      <md-input
                        name="categoria"
                        id="categoria"
                        autocomplete="text"
                        type="text"
                        v-model="form.categoria"
                      />
                      <span class="md-error">El campo no puede ir vacio</span>
                    </md-field>
                  </div>
                </div>

                <div class="md-layout md-gutter md-alignment-center">
                  <div class="md-layout-item md-size-100">
                    <md-field :class="getValidationClass('descripcion')">
                      <label for="descripcion">Descripcin</label>
                      <md-textarea
                        name="descripcion"
                        id="descripcion"
                        autocomplete="text"
                        type="text"
                        v-model="form.descripcion"
                      />
                      <span class="md-error">El campo no puede ir vacio</span>
                    </md-field>
                  </div>
                </div>

                

                <md-field>
                  <label>Imagen de portada</label>
                  <md-file
                    v-model="imgCategoria"
                    @change="getFile"
                    accept="image/*"
                  />
                </md-field>

                <md-field>
                  <label>Titulo en imagen de categoria</label>
                  <md-file
                    v-model="imgCategoriaTitle"
                    @change="getFileTitle"
                    accept="image/*"
                  />
                </md-field>

                <div class="md-layout md-gutter md-alignment-center">
                  <md-progress-spinner
                    :md-diameter="30"
                    :md-stroke="3"
                    md-mode="indeterminate"
                    v-if="sedingInfo"
                  ></md-progress-spinner>
                </div>

                <div class="md-layout md-gutter">
                  <div class="md-layout-item md-alignment-left">
                    <md-checkbox class="chk-activo" v-model="boolean"
                      >Activo</md-checkbox
                    >
                  </div>
                </div>

                <md-card-actions class="md-layout md-gutter md-alignment-right">
                  <md-button
                    type="submit"
                    class="md-raised md-accent btnEntrar"
                    :disabled="sedingInfo"
                    >Guardar</md-button
                  >
                </md-card-actions>
                <md-snackbar :md-active.sync="showSnackbar">{{
                  alertMessage
                }}</md-snackbar>
              </form>
            </div>
          </div>
          <div
            class="md-layout-item md-xsmall-size-100 md-small-size-40  md-size-50 "
          >
            <md-card>
            <div class="image_edit" v-bind:style="{ 'background-image': 'url(\'' + form.ubicacion + '\')', fontSize: fontSize + 'px' }">
                
              </div>
            </md-card>
            <md-card>
              <div class="image_edit" v-bind:style="{ 'background-image': 'url(\'' + form.imagen_titulo_categoria + '\')', fontSize: fontSize + 'px' }">
                
              </div>
            </md-card>
          </div>
          <div class="md-layout-item"></div>
        </div>
      </md-content>
    </div>
  </div>
</template>

<script>
import { validationMixin } from "vuelidate";
import {
  required,
  email,
  minLength,
  maxLength,
} from "vuelidate/lib/validators";

import SideNav from "@/components/SideNav.vue";

// @ is an alias to /src
export default {
  name: "EditarCategoria",
  components: {
    SideNav,
  },
  props: {
    info: String,
  },
  validations: {
    form: {
      categoria: {
        required,
      },
    },
  },
  data() {
    return {
      boolean: false,
      users: [],
      marcas: [],
      sedingInfo: true,
      form: {
        id: null,
        categoria: null,
        descripcion: null,
        id_rol: null,
        activo: null,
        ubicacion: null,
        imagen_titulo_categoria: null
      },
      sedingInfo: true,
      alertMessage: "",
      showSnackbar: false,
      imgCategoria: null,
      imgCategoriaTitle: null,
      file: null,
      fileCategoriaTitle: null
    };
  },
  mounted() {

    if (localStorage.getItem("tokenUser_et") === "") {
      this.$router.push("/");
    }
    const baseURI = this.$baseURL + "menudigital/index.php/categoria/update";
    this.$http
      .get(
        baseURI,
        { params: { id: this.$route.params.id, token: localStorage.getItem("tokenUser_et") } },
        { "Access-Control-Allow-Origin": "*" }
      )
      .then((res) => {
        if (res.data.status === "OK") {
          //mostrarSucursales
          this.form.id = res.data.registro.id;
          this.form.categoria = res.data.registro.nombre;
          this.form.descripcion = res.data.registro.descripcion;
          this.form.activo = res.data.registro.activo;
          this.form.ubicacion = res.data.registro.ubicacion;
          this.form.imagen_titulo_categoria = res.data.registro.imagen_titulo_categoria;
          this.sedingInfo = false;
          if (this.form.activo == 1) {
            this.boolean = true;
          } else {
            this.boolean = false;
          }
        }

        if (res.data.status.toUpperCase() === "ERROR") {
          this.alertMessage = res.data.respuesta;
          this.showSnackbar = true;
          this.sedingInfo = false;
          this.$router.push("/");
        }
      });
  },
  methods: {
    getValidationClass(fieldName) {
      const field = this.$v.form[fieldName];

      if (field) {
        return {
          "md-invalid": field.$invalid && field.$dirty,
        };
      }
    },
    clearForm() {
      this.$v.$reset();
    },
    saveRegion() {
      this.sedingInfo = true;
      if (this.boolean) {
        this.form.activo = 1;
      } else {
        this.form.activo = 0;
      }
      let formData = new FormData();
      formData.append("file", this.file);
      formData.append("imagen_titulo_categoria", this.fileCategoriaTitle);
      formData.append("token", localStorage.getItem("tokenUser_et"));
      formData.append("nombre", this.form.categoria);
      formData.append("descripcion", this.form.descripcion);
      formData.append("id", this.form.id);
      formData.append("activo", this.form.activo);
      var a = this;
      const baseURI = this.$baseURL + "menudigital/index.php/categoria/update_db";
      this.$http
        .post(baseURI, formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((res) => {
          console.log(res);
          if (res.data.status != null) {
            if (res.data.status.toUpperCase() === "OK") {
              this.$router.push("/verCategorias");
            }

            if (res.data.status.toUpperCase() === "ERROR") {
              this.alertMessage = res.data.respuesta;
              this.showSnackbar = true;
              this.sending = false;
            }

            if (
              res.data.status.toUpperCase() != "OK" &&
              res.data.status.toUpperCase() != "ERROR"
            ) {
              this.alertMessage =
                "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
              this.showSnackbar = true;
              this.sending = false;
            }
          } else {
            this.alertMessage =
              "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
            this.showSnackbar = true;
            this.sedingInfo = false;
          }
          //
        })
        .catch(function(error) {
          // handle error
          console.log(error);
          a.alertMessage =
            "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
          a.showSnackbar = true;
          a.sedingInfo = false;
        });
    },
    validateCategoria() {
      this.$v.$touch();

      if (!this.$v.$invalid) {
        this.saveRegion();
      }
    },
    getFile() {
      this.file = this.imgCategoria.srcElement.files[0];
    },
    getFileTitle(){
    this.fileCategoriaTitle = this.imgCategoriaTitle.srcElement.files[0];
    }
  },
};
</script>

