<template>
  <div class="addCategoria">
    <SideNav />
    <div class="container-left">
      <md-content>
        <h2 class="title-view">Agregar Subcategoria</h2>
        <md-divider></md-divider>
        <div class="md-layout md-gutter">
          <div class="md-layout-item md-xsmall-size-100 md-small-size-60 md-size-50">
            <div>
              <form novalidate class @submit.prevent="validateRegion">
                <div class="md-layout md-gutter md-alignment-center">
                  <div class="md-layout-item md-size-100">
                    <md-field :class="getValidationClass('subcategoria')">
                      <label for="subcategoria">Nombre</label>
                      <md-input name="subcategoria" id="categoria" autocomplete="text" type="text" v-model="form.subcategoria"/>
                      <span class="md-error">El campo no puede ir vacio</span>
                    </md-field>
                  </div>
                </div>

                <div class="md-layout md-gutter md-alignment-center">
                  <div class="md-layout-item md-size-100">
                    <md-field :class="getValidationClass('descripcion')">
                      <label for="descripcion">Descripcin</label>
                      <md-input name="descripcion" id="descripcion" autocomplete="text" type="text" v-model="form.descripcion"/>
                      <span class="md-error">El campo no puede ir vacio</span>
                    </md-field>
                  </div>
                </div>

                <div class="md-layout md-gutter">
                  <div class="md-layout-item">
                    <md-field :class="getValidationClass('id_categoria')">
                      <label for="id_categoria">Categoria</label>
                      <md-select
                        v-model="categorias2"
                        name="id_categoria"
                        id="id_categoria"
                        :disabled="sedingInfo"
                        multiple
                      >
                        <md-option
                          v-for="(item, key) in categorias"
                          :key="key"
                          :value="item.id"
                        >
                          {{ item.nombre }}
                        </md-option>
                      </md-select>
                      <span class="md-error"
                        >Por favor selecciona una categoria</span
                      >
                    </md-field>
                  </div>
                </div>

                <div class="md-layout md-gutter">
                  <div class="md-layout-item">
                    <md-field :class="getValidationClass('id_region')">
                      <label for="id_region">Region</label>
                      <md-select
                        v-model="regiones2"
                        name="id_region"
                        id="id_region"
                        :disabled="sedingInfo"
                        multiple
                      >
                        <md-option
                          v-for="(item, key) in regiones"
                          :key="key"
                          :value="item.id"
                        >
                          {{ item.nombre }}
                        </md-option>
                      </md-select>
                      <span class="md-error"
                        >Por favor selecciona una regin</span
                      >
                    </md-field>
                  </div>
                </div>

                <div class="md-layout md-gutter md-alignment-center">
                  <md-progress-spinner
                    :md-diameter="30"
                    :md-stroke="3"
                    md-mode="indeterminate"
                    v-if="sedingInfo"
                  ></md-progress-spinner>
                </div>

                <md-card-actions class="md-layout md-gutter md-alignment-right">
                  <md-button
                    type="submit"
                    class="md-raised md-accent btnEntrar"
                    :disabled="sedingInfo"
                  >Guardar</md-button>
                </md-card-actions>
                <md-snackbar :md-active.sync="showSnackbar">{{alertMessage}}</md-snackbar>
              </form>
            </div>
          </div>
          <div class="md-layout-item md-xsmall-size-100 md-small-size-40  md-size-50 ">
            
          </div>
          <div class="md-layout-item"></div>
        </div>
      </md-content>
    </div>
  </div>
</template>

<script>
import { validationMixin } from "vuelidate";
import {
  required,
  email,
  minLength,
  maxLength
} from "vuelidate/lib/validators";

import SideNav from "@/components/SideNav.vue";

// @ is an alias to /src
export default {
  name: "AgregarCategoria",
  components: {
    SideNav
  },
  validations: {
    form: {
      subcategoria: {
        required
      },
    }
  },
  data() {
    return {
      users: [],
      categorias: [],
      categorias2: [],
      regiones: [],
      regiones2: [],
      form: {
        subcategoria: null,
        descripcion: null,
        descripcion_platillos: null,
        id_categoria: null,
        id_region: null,
      },
      sedingInfo: false,
      alertMessage: "",
      showSnackbar: false,
    };
  },
  created() {
    
    if (localStorage.getItem("tokenUser_et") === "") {
      this.$router.push("/");
    } 
  },
  methods: {
    getValidationClass(fieldName) {
      const field = this.$v.form[fieldName];

      if (field) {
        return {
          "md-invalid": field.$invalid && field.$dirty
        };
      }
    },
    clearForm() {
      this.$v.$reset();
    },
    saveRegion(){
      this.sedingInfo = true;
      let formData = new FormData();
      formData.append("token", localStorage.getItem("tokenUser_et"));
      formData.append("nombre", this.form.subcategoria);
      formData.append("descripcion", this.form.descripcion);
      formData.append("id_categoria",this.form.id_categoria);
      formData.append("categorias", this.categorias2.toString());
      formData.append("regiones", this.regiones2.toString());
      //formData.append("id_marca", this.form.id_marca);
      //formData.append("id_region", this.form.id_region);
      //alert(this.form.unidad);
      var a = this;
      const baseURI = this.$baseURL + "menudigital/index.php/subcategoria/add_db";
      this.$http
        .post(
          baseURI,
             formData,
             {headers: {
            "Content-Type": "multipart/form-data",
          }}
        )
        .then(res => {
          console.log(res);
          if (res.data.status != null) {
            if (res.data.status.toUpperCase() === "OK") {
              this.$router.push("/verSubCategorias");
            }

            if (res.data.status.toUpperCase() === "ERROR") {
              this.alertMessage = res.data.respuesta;
              this.showSnackbar = true;
              this.sending = false;
            }

            if (
              res.data.status.toUpperCase() != "OK" &&
              res.data.status.toUpperCase() != "ERROR"
            ) {
              this.alertMessage =
                "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
              this.showSnackbar = true;
              this.sending = false;
            }
          } else {
            this.alertMessage = "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
              this.showSnackbar = true;
              this.sedingInfo = false;
          }
          //
        })
        .catch(function(error) {
          // handle error
          console.log(error);
          a.alertMessage = "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
          a.showSnackbar = true;
          a.sedingInfo = false;
        });
    },
    validateRegion() {
      this.$v.$touch();

      if (!this.$v.$invalid) {
        this.saveRegion();
      }
    },
  },
  mounted(){
    const baseURI = this.$baseURL + "menudigital/index.php/subcategoria/add";
    this.$http
      .get(
        baseURI,
        { params: {  token: localStorage.getItem("tokenUser_et"),
        id_marca: localStorage.getItem("id_marca_et") } },
        { "Access-Control-Allow-Origin": "*" }
      )
      .then((res) => {
        //this.users = result.data;
        console.log(res.data);
        if(res.data.status != null){
          if (res.data.status === "OK") {
          this.categorias = res.data.categorias;
          this.regiones = res.data.regiones;
          //this.sedingInfo = false;
        }

        if (res.data.status.toUpperCase() === "ERROR") {
          this.alertMessage = res.data.respuesta;
          this.showSnackbar = true;
          this.sedingInfo = false;
          this.$router.push("/");
        }
        }
        //
      });
  }
};
</script>

