<template>
  <div class="verRegiones">
    <SideNav />
    <div class="container-left">
      <md-content>
        <h2 class="title-view">Unidades</h2>
        <md-divider></md-divider>
        <div class="md-layout md-gutter md-alignment-center">
          <md-progress-spinner
            :md-diameter="30"
            :md-stroke="3"
            md-mode="indeterminate"
            v-if="sendingInfo"
          ></md-progress-spinner>
        </div>
        <div class></div>
        <div class="md-layout md-gutter">
          <div
            class="md-layout-item md-xsmall-size-100 md-small-size-100 md-large-size-100 md-size-80"
          >
            <div>
              <md-table
                v-model="searched"
                md-sort="name"
                md-sort-order="asc"
                md-card
                md-fixed-header
                class="table-unidades"
              >
                <md-table-toolbar>
                  <md-field
                    md-clearable
                    class="md-toolbar-section-rigth md-layout-item md-size-60 input-buscarRegistros"
                  >
                    <md-input
                      class="md-size-10"
                      placeholder="Buscar por nombre..."
                      v-model="search"
                      @input="searchOnTable"
                    />
                  </md-field>
                </md-table-toolbar>

                <md-table-empty-state
                  md-label="Unidad no encontrada"
                  :md-description="
                    `No se encontrol la unidad '${search}'. Itenta con otro nombre o registra una nueva`
                  "
                >
                  <router-link to="/addUnidad">
                    <md-button class="md-primary md-raised"
                      >Agregar nueva unidad</md-button
                    >
                  </router-link>
                </md-table-empty-state>

                <md-table-row slot="md-table-row" slot-scope="{ item }" class>
                  <md-table-cell md-label="Nombre" md-sort-by="name">{{
                    item.nombre
                  }}</md-table-cell>
                  <md-table-cell md-label="Latitud" md-sort-by="email">{{
                    item.latitud
                  }}</md-table-cell>
                  <md-table-cell md-label="Longitud" md-sort-by="gender">{{
                    item.longitud
                  }}</md-table-cell>
                  <md-table-cell md-label="Activo" md-sort-by="title">{{
                    item.activo
                  }}</md-table-cell>
                  <md-table-cell md-label="Opciones" class="cell-opciones">
                    <md-button class="btnEdit" @click="showDialogDelete(item.id)">
                      <md-icon>delete</md-icon>
                    </md-button>
                    <router-link :to="'/editarUnidad/' + item.id">
                      <md-icon @click="active = true">edit</md-icon>
                    </router-link>
                  </md-table-cell>
                </md-table-row>
              </md-table>
            </div>
          </div>
          <div
            class="md-layout-item md-xsmall-size-100 md-small-size-100 md-size-100"
          ></div>
          <div class="md-layout-item"></div>
        </div>

        <div>
          <md-dialog-confirm
            :md-active.sync="active"
            md-title="Alerta"
            md-content="Desea eliminar esta unidad?"
            md-confirm-text="Aceptar"
            md-cancel-text="Cancelar"
            @md-cancel="onCancel"
            @md-confirm="onConfirm"
          />

        </div>
      </md-content>
    </div>
    <md-snackbar :md-active.sync="showSnackbar">{{ alertMessage }}</md-snackbar>
  </div>
</template>

<script>
const toLower = (text) => {
  return text.toString().toLowerCase();
};

const searchByName = (items, term) => {
  if (term) {
    return items.filter((item) => toLower(item.nombre).includes(toLower(term)));
  }

  return items;
};

import SideNav from "@/components/SideNav.vue";

// @ is an alias to /src
export default {
  name: "VerRegiones",
  data: () => ({
    sendingInfo: true,
    id_marca: 0,
    active: false,
    value: null,
    alertMessage: "",
    showSnackbar: false,
    search: null,
    searched: [],
    users: [],
    unidades: [],
    idDelete: null
  }),

  mounted() {
    this.obtenerUnidad();
  },
  components: {
    SideNav,
  },
  methods: {
    onConfirm() {
      var context = this;
      this.value = "Agreed";
      this.sedingInfo = true;
      let formData = new FormData();
      formData.append("token", localStorage.getItem("tokenUser_et"));
      formData.append("id", this.idDelete);

      const baseURI = this.$baseURL + "menudigital/index.php/sucursal/delete";
      this.$http
        .post(
          baseURI,
             formData,
          { "Access-Control-Allow-Origin": "*" }
        )
        .then(res => {
          if (res.data.status != null) {
            if (res.data.status.toUpperCase() === "OK") {
              this.obtenerUnidad();
            }

            if (res.data.status.toUpperCase() === "ERROR") {
              this.alertMessage = res.data.respuesta;
              this.showSnackbar = true;
              this.sending = false;
            }

            if (
              res.data.status.toUpperCase() != "OK" &&
              res.data.status.toUpperCase() != "ERROR"
            ) {
              this.alertMessage =
                "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
              this.showSnackbar = true;
              this.sending = false;
            }
          } else {
            context.alertMessage =
                "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
              context.showSnackbar = true;
              context.sedingInfo = false;
          }
          //
        })
        .catch(function(error) {
          // handle error
          console.log(error);
          context.alertMessage =
            "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
          context.showSnackbar = true;
          context.sending = false;
        });
    },
    obtenerUnidad(){
      this.users = [];
      

    if (localStorage.getItem("tokenUser_et") === "") {
      this.$router.push("/");
    } 

    const baseURI = this.$baseURL + "menudigital/index.php/sucursal";
    this.$http
      .get(
        baseURI,
        { params: { id_marca: localStorage.getItem("id_marca_et"), token: localStorage.getItem("tokenUser_et") } },
        { "Access-Control-Allow-Origin": "*" }
      )
      .then((res) => {
        //this.users = result.data;
        console.log(res.data.registros);
        if (res.data.status === "OK") {
          //mostrarSucursales
          this.sedingInfo = false;
          res.data.registros.map((item) => {
            const obj = {
              id: item.id,
              nombre: item.nombre === null ? "2" : item.nombre,
              registro: item.registro,
              activo: item.activo,
              latitud: item.latitud,
              longitud: item.longitud,
              id_region: item.id_region,
              id_marca: item.id_marca,
            };
            this.users.push(obj);
            console.log(item.nombre);
            this.sending = false;
          });
          this.sendingInfo = false;

          //this.searched = this.users;
          //this.unidades = res.data.registros;
          //this.searched = res.data.registros;
        }

        if (res.data.status.toUpperCase() === "ERROR") {
          this.alertMessage = res.data.respuesta;
          this.showSnackbar = true;
          this.sending = false;
          this.$router.push("/");
        }
        this.searched = this.users;
        //
      });
    },
    onCancel() {
      this.value = "Disagreed";
    },
    newUser() {
      window.alert("Noop");
    },
    searchOnTable() {
      //alert(this.search);
      //console.log(this.users);
      //this.searched = this.users//;
      this.searched = searchByName(this.users, this.search);
      //
      //this.searched = this.filteredClients;
      console.log(this.searched);
    },
    showDialogDelete(id){
      this.active = true;
      this.idDelete = id;
    }
  },
};
</script>

