<script>
import { validationMixin } from "vuelidate";
import {
  required,
  email,
  minLength,
  maxLength,
} from "vuelidate/lib/validators";
import SideNav from "@/components/SideNav.vue";
import { gmapApi } from "vue2-google-maps";

// @ is an alias to /src
export default {
  name: "AgregarRegion",
  components: {
    SideNav,
  },
  computed: {
    google: gmapApi,
  },
  validations: {
    form: {
      platillo: {
        required,
      },
      precio_nacional: {
        required,
      },
      precio_cc: {
        required,
      },
      precio_tijuana: {
        required,
      },
      precio_pl: {
        required,
      },
      precio_acapulco: {
        required,
      },
      id_subcategoria: {
        required,
      },
    },
  },
  mounted() {
    if (localStorage.getItem("tokenUser_et") === "") {
      this.$router.push("/");
    }
    this.id_marca = localStorage.getItem("id_marca_et");
    const baseURI = this.$baseURL + "menudigital/index.php/articulo/update";
    this.$http
      .get(
        baseURI,
        {
          params: {
            id: this.$route.params.id,
            token: localStorage.getItem("tokenUser_et"),
            id_marca: localStorage.getItem("id_marca_et")
          },
        },
        { "Access-Control-Allow-Origin": "*" }
      )
      .then((res) => {
        //this.users = result.data;
        console.log(res.data);
        if (res.data.status === "OK") {
          //mostrarSucursales
          this.form.id_producto = res.data.registro.id;
          this.form.numero_orden = res.data.registro.orden;
          this.sucursales = res.data.sucursales;
          this.subcategorias = res.data.subcategorias;
          this.medidas = res.data.medidas;
          this.experiencias = res.data.experiencias;
          //this.extras = res.data.extras;
          this.experiencias.push({ nombre: " Ninguna", id: -1 });
          this.categorias = res.data.categorias;
          this.form.platillo = res.data.registro.platillo;
          this.form.descripcion = res.data.registro.descripcion;
          this.form.activo = res.data.registro.activo;
          this.form.ubicacion = res.data.registro.ubicacion;
          this.form.precio_cc = res.data.registro.precio_cc;
          this.form.precio_nacional = res.data.registro.precio_nacional;
          this.form.precio_tijuana = res.data.registro.precio_tijuana;
          this.form.precio_pl = res.data.registro.precio_pl;
          this.form.precio_acapulco = res.data.registro.precio_acapulco;
          this.form.precio_aeropuerto = res.data.registro.precio_aeropuerto;
          this.form.id_sucursal = res.data.registro.id_sucursal;
          this.form.id_categoria = res.data.registro.id_categoria;
          this.form.id_subcategoria = res.data.registro.id_subcategoria;
          this.experiencias2 = (res.data.registro.id_experiencia === '') ? [] : res.data.registro.id_experiencia.split(",");
          //this.extras2 = (res.data.registro.extra === '') ? [] : res.data.registro.extra.split(",");
          //this.extras2 = res.data.registro.id_extra.split(",");
          this.form.id_experiencia =
            res.data.registro.id_experiencia !== null
              ? res.data.registro.id_experiencia
              : null;
          this.form.logo = res.data.registro.logo;
          this.form.simbologia = res.data.registro.simbologia;
          this.form.cantidad_x_porcion = res.data.registro.cantidad_x_porcion;
          this.form.id_medida = res.data.registro.id_medida;
          this.form.descripcion_imagen = res.data.registro.descripcion_imagen;
          this.form.detalle_imagen = res.data.registro.detalle_imagen;
          
          //alert(this.medidasXProd.length);
          if (res.data.registro.medidas.length > 0) {
            this.medidasXProd = res.data.registro.medidas;
            this.indicarMedidas = true;
            ;
            // alert("---");
          }
          this.sedingInfo = false;
          if (this.form.activo == 1) {
            this.boolean = true;
          } else {
            this.boolean = false;
          }
        }
        this.sedingInfo = false;

        if (res.data.status.toUpperCase() === "ERROR") {
          this.alertMessage = res.data.respuesta;
          this.showSnackbar = true;
          this.sedingInfo = false;
          this.$router.push("/");
        }

        if (this.form.activo == 1) {
          this.boolean = true;
        } else {
          this.boolean = false;
        }
        //
      });
  },
  props: {
    info: String,
  },
  data() {
    return {
      booleanMedida: false,
      boolean: false,
      indicarMedidas: false,
      agregarImagenSecundaria: false,
      experiencias: [],
      experiencias2: [],
      categorias: [],
      categorias2: [],
      extras: [],
     /* extras2: [],*/
      subcategorias: [],
      sucursales: [],
      medidasXProd: [
        {
          numero: 1,
          nombre_medida: "",
          cantidad_x_porcion_medida: "",
          precio_nacional: 0,
          precio_acapulco: 0,
          precio_cc: 0,
          precio_tijuana: 0,
          precio_pl: 0,
          precio_aeropuerto: 0,
          activo: "1"
        },
        {
          numero: 2,
          nombre_medida: "",
          cantidad_x_porcion_medida: "",
          precio_nacional: 0,
          precio_acapulco: 0,
          precio_cc: 0,
          precio_tijuana: 0,
          precio_pl: 0,
          precio_aeropuerto: 0,
          activo: "1"
        },
      ],
      users: [],
      markers: [],
      marker: { position: { lat: 19.4284706, lng: -99.1276627 } },
      sedingInfo: true,
      alertMessage: "",
      showSnackbar: false,
      single: null,
      imgSecundaria: null,
      form: {
        id_producto: null,
        platillo: null,
        cantidad_x_porcion: null,
        id_marca: null,
        id_categoria: null,
        id_subcategoria: null,
        id_experiencia: null,
        descripcion: "",
        simbologia: "",
        id_sucursal: null,
        precio_nacional: null,
        precio_cc: null,
        precio_tijuana: null,
        precio_pl: null,
        precio_acapulco: null,
        precio_aeropuerto: null,
        logo: null,
        activo: null,
        id_medida: null,
        numero_orden: 0,
        descripcion_imagen: "",
        detalle_imagen: null,
        ubicacion: null
      },
      fileImgSecundaria: null,
      file: null,
    };
  },
  methods: {
    eliminar(index) {
      if (this.medidasXProd.length > 2) {
        this.medidasXProd.splice(index, 1);
      } else {
        alert(
          "Si solo desea agregar una medida, desmarque la opcin de agregar medidas"
        );
      }
    },
    medidaActiva(medida){
      //alert(medida.precio_aeropuerto);
      if(this.booleanMedida){
       // alert();
      }
    },
    agregarMedida() {
      const obj = {
        numero: this.medidasXProd.length + 1,
        nombre_medida: "",
        cantidad_x_porcion_medida: "",
        precio_nacional: 0,
        precio_acapulco: 0,
        precio_cc: 0,
        precio_tijuana: 0,
        precio_pl: 0,
        precio_aeropuerto: 0,
        activo: "1"
      };
      this.medidasXProd.push(obj);
    },
    clickMakers: function(position) {
      console.log(position.position);
      this.form.latitud = position.position.lat;
      this.form.longitud = position.position.lng;
      this.form.unidad = position.name;
    },
    clickMaker: function(position) {
      console.log(position.position);
    },
    changeMedidas() {
      if (indicarMedidas === false) {
        alert();
      }
    },
    clickMapa: function(event) {
      console.log(event);
      this.marker.position.lat = event.latLng.lat();
      this.marker.position.lng = event.latLng.lng();
      this.form.latitud = event.latLng.lat();
      this.form.longitud = event.latLng.lng();
    },
    getValidationClass: function(fieldName) {
      const field = this.$v.form[fieldName];

      if (field) {
        return {
          "md-invalid": field.$invalid && field.$dirty,
        };
      }
    },
    clearForm() {
      this.$v.$reset();
    },
    saveUnidad() {
      this.sedingInfo = true;
      if (this.boolean) {
        this.form.activo = 1;
      } else {
        this.form.activo = 0;
      }

      let formData = new FormData();
      formData.append("file", this.file);
      formData.append("detalle_imagen", this.fileImgSecundaria);
      formData.append("token", localStorage.getItem("tokenUser_et"));
      formData.append("id", this.form.id_producto);
      formData.append("platillo", this.form.platillo);
      formData.append("nombre", this.form.platillo);
      formData.append("id_categoria", this.form.id_categoria);
      formData.append("id_subcategoria", this.form.id_subcategoria);
      
      //if(this.experiencias2.length !== 0){
        formData.append("id_experiencia", this.experiencias2.toString());
        //formData.append("extra", this.extras2.toString());
      /*}else{
        formData.append("id_experiencia","");
      }*/
//formData.append("extras", this.extras2.toString());
//if(this.categorias2.length !== 0){
       // formData.append("categorias", this.categorias2.toString());
/*}else{
  formData.append("categorias", "");
}*/
      formData.append("orden", this.form.numero_orden);
      formData.append("logo", this.form.logo);
      formData.append("descripcion", this.form.descripcion);
      formData.append("simbologia", this.form.simbologia);
      formData.append("id_sucursal", this.form.id_sucursal);
      formData.append("id_medida", this.form.id_medida);
      formData.append("nacional", this.form.precio_nacional);
      formData.append("precio_aeropuerto", this.form.precio_aeropuerto);
      formData.append("cc", this.form.precio_cc);
      formData.append("tijuana", this.form.precio_tijuana);
      formData.append("pl", this.form.precio_pl);
      formData.append("activo", this.form.activo);
      formData.append("acapulco", this.form.precio_acapulco);
      //formData.append("categorias", this.categorias2.toString());
      formData.append("cantidad_x_porcion", this.form.cantidad_x_porcion);
      formData.append("descripcion_imagen", this.form.descripcion_imagen);

      if (this.indicarMedidas !== false) {
        //console.log(JSON.stringify(this.medidasXProd));
        formData.append("medidas", JSON.stringify(this.medidasXProd));
      } else {
        formData.append("medidas", "");
      }

      console.log(this.$route.params.id);
      const baseURI =
        this.$baseURL + "menudigital/index.php/articulo/update_db";
      //alert(baseURI);
      this.$http
        .post(baseURI, formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((res) => {
          //this.users = result.data;
          console.log(res);
          if (res.data.status != null) {
            if (res.data.status.toUpperCase() === "OK") {
              this.$router.push("/verProductos");
            }

            if (res.data.status.toUpperCase() === "ERROR") {
              this.alertMessage = res.data.respuesta;
              this.showSnackbar = true;
              this.sedingInfo = false;
            }

            if (
              res.data.status.toUpperCase() != "OK" &&
              res.data.status.toUpperCase() != "ERROR"
            ) {
              this.alertMessage =
                "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
              this.showSnackbar = true;
              this.sedingInfo = false;
            }
          } else {
            this.alertMessage =
              "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
            this.showSnackbar = true;
            this.sedingInfo = false;
          }
          //
        })
        .catch(function(error) {
          // handle error
          //console.log(error);
          this.alertMessage =
            "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
          this.showSnackbar = true;
          this.sedingInfo = false;
        });
    },
    validateProducto() {
      this.$v.$touch();

      if (!this.$v.$invalid) {
        this.saveUnidad();
      }
    },
    open() {
      console.log(this.single.srcElement.files[0]);
      this.file = this.single.srcElement.files[0];
    },
    openImgSecundaria() {
      //console.log(this.imgSecundaria.srcElement.files[0]);
      this.fileImgSecundaria = this.imgSecundaria.srcElement.files[0];
    },
    obtenerArticulos() {
      var a = this;
      const baseURI = this.$baseURL + "menudigital/index.php/articulo/update";
      this.$http
        .get(
          baseURI,
          { params: { token: localStorage.getItem("tokenUser_et"),id_marca: localStorage.getItem("id_marca_et") } },
          { "Access-Control-Allow-Origin": "*" }
        )
        .then((res) => {
          //this.users = result.data;
          console.log(res.data.subcategorias);
          if (res.data.status != null) {
            if (res.data.status === "OK") {
              a.categorias = res.data.categorias;
              a.subcategorias = res.data.subcategorias;
            }

            if (res.data.status.toUpperCase() === "ERROR") {
              this.alertMessage = res.data.respuesta;
              this.showSnackbar = true;
              this.sedingInfo = false;
              this.$router.push("/");
            }
          }
          //
        });
    },
  },
};
</script>

