                        v-model="form.latitud"
                      />
                      <span class="md-error">El campo no puede ir vacio</span>
                    </md-field>
                    <a class="link-ayuda" @click="showDialog = true">Como obtener coordenadas?</a>
                  </div>
                </div>

                <div class="md-layout md-gutter md-alignment-center">
                  <div class="md-layout-item md-size-100">
                    <md-field :class="getValidationClass('longitud')">
                      <label for="longitud">Longitud</label>
                      <md-input
                        name="longitud"
                        id="longitud"
                        autocomplete="text"
                        type="text"
                        v-model="form.longitud"
                      />
                      <span class="md-error">El campo no puede ir vacio</span>
                    </md-field>
                  </div>
                </div>

                <div class="md-layout md-gutter">
                  <div class="md-layout-item">
                    <md-field :class="getValidationClass('id_region')">
                      <label for="id_region">Region</label>
                      <md-select
                        v-model="form.id_region"
                        name="id_region"
                        id="id_region"
                        :disabled="sedingInfo"
                      >
                        <md-option
                          v-for="(itemRegion, key) in regiones"
                          :key="key"
                          :value="itemRegion.id"
                        >
                          {{ itemRegion.nombre }}
                        </md-option>
                      </md-select>
                      <span class="md-error"
                        >Por favor selecciona una region</span
                      >
                    </md-field>
                  </div>
                </div>

                <div class="md-layout md-gutter">
                  <div class="md-layout-item">
                    <md-field :class="getValidationClass('id_alcohol')">
                      <label for="id_alcohol">Bebidas alcoholicas</label>
                      <md-select
                        v-model="form.id_alcohol"
                        name="id_alcohol"
                        id="id_alcohol"
                        :disabled="sedingInfo"
                      >
                        <md-option
                          v-for="(itemAlcohol, key) in alcohol"
                          :key="key"
                          :value="itemAlcohol.id"
                        >
                          {{ itemAlcohol.nombre }}
                        </md-option>
                      </md-select>
                      <span class="md-error"
                        >Por favor selecciona una opcion</span
                      >
                    </md-field>
                  </div>
                </div>

                <div class="md-layout md-gutter">
                  <div class="md-layout-item">
                    <md-field :class="getValidationClass('id_estado')">
                      <label for="id_estado">Estado</label>
                      <md-select
                        v-model="form.id_estado"
                        name="id_estado"
                        id="id_estado"
                        :disabled="sedingInfo"
                      >
                        <md-option
                          v-for="(itemEstado, key) in estados"
                          :key="key"
                          :value="itemEstado.id"
                        >
                          {{ itemEstado.nombre }}
                        </md-option>
                      </md-select>
                      <span class="md-error"
                        >Por favor selecciona una region</span
                      >
                    </md-field>
                  </div>
                </div>


                <div class="md-layout md-gutter md-alignment-center">
                  <md-progress-spinner
                    v-if="sedingInfo"
                    :md-diameter="30"
                    :md-stroke="3"
                    md-mode="indeterminate"
                  ></md-progress-spinner>
                </div>
                <md-snackbar :md-active.sync="showSnackbar">{{
                  alertMessage
                }}</md-snackbar>
                <md-card-actions class="md-layout md-gutter md-alignment-right">
                  <md-button type="submit" class="md-raised md-accent btnEntrar"
                    >Guardar</md-button
                  >
                </md-card-actions>
                <md-snackbar>The user was saved with success!</md-snackbar>
              </form>
            </div>
          </div>
          <div
            class="md-layout-item md-xsmall-size-100 md-small-size-40 md-size-60"
          >
            
            <md-card-actions
              v-if="false"
              class="md-layout md-gutter md-alignment-right"
            >
              <md-button
                class="md-raised md-accent btnEntrar"
                @click="buscarToks"
              >
                <md-icon>search</md-icon>Buscar restaurantes Toks
              </md-button>
            </md-card-actions>
            <GmapMap
              v-if="false"
              ref="mapRef"
              :center="marker.position"
              :zoom="12"
              map-type-id="terrain"
              style="width: 100%; height: 50vh"
              @click="clickMapa"
            >
              <GmapMarker
                ref="myMarker"
                :position="m.position"
                @click="clickMakers(m)"
                :key="index"
                v-for="(m, index) in markers"
                :icon="{ url: require('../assets/img/pin-rest.png') }"
              />
              <GmapMarker
                ref="myMarker"
                :position="marker.position"
                @click="clickMaker(marker.position)"
              />
            </GmapMap>
          </div>
          <div class="md-layout-item"></div>
