      </md-content>
    </div>

  <md-dialog :md-active.sync="showDialog">
      <md-dialog-title>Ayuda</md-dialog-title>

      <md-tabs md-dynamic-height>
        <md-tab md-label="Como obener coordenadas?">
          <p>- Abre <a href="https://www.google.com/maps">Google Maps</a> en el ordenador. Si utilizas el modo bsico de
              Google Maps, vers un relmpago en la parte inferior y no podrs
              obtener las coordenadas de los sitios.</p>
          <p>- Haz clic con el botn derecho en el sitio o en el rea del mapa.</p>
          <p>- Selecciona Qu hay aqu?</p>
          <p>- En la parte inferior, vers una tarjeta con las coordenadas.</p>
        </md-tab>

      </md-tabs>

      <md-dialog-actions>
        <md-button class="md-primary" @click="showDialog = false">Cerrar</md-button>
      </md-dialog-actions>
    </md-dialog>

