<script>
import { validationMixin } from "vuelidate";
import {
  required,
  email,
  minLength,
  maxLength,
} from "vuelidate/lib/validators";

import SideNav from "@/components/SideNav.vue";
import { gmapApi } from "vue2-google-maps";
var id_marca = "";

// @ is an alias to /src
export default {
  name: "AgregarUnidad",
  computed: {
    google: gmapApi,
  },
  components: {
    SideNav,
  },
  props: {
    info: String,
  },
  validations: {
    form: {
      unidad: {
        required,
      },
      latitud: {
        required,
      },
      longitud: {
        required,
      },
      id_region: {
        required,
      },
      id_estado: {
        required,
      },
    },
  },
  created() {
    if (localStorage.getItem("tokenUser_et") === "") {
      this.$router.push("/");
    }
  },
  data() {
    return {
      showDialog: false,
      users: [],
      markers: [],
      marker: { position: { lat: 19.4284706, lng: -99.1276627 } },
      sedingInfo: false,
      alertMessage: "",
      showSnackbar: false,
      nombre_user: null,
      estados: [],
      regiones: [],
      alcohol: [],
      form: {
        id_unidad: null,
        unidad: null,
        latitud: null,
        longitud: null,
        id_region: null,
        id_marca: null,
        id_estado: null,
        id_alcohol: null,
      },
    };
  },
  methods: {
    clickMakers: function(position) {
      console.log(position.position);
      this.form.latitud = position.position.lat;
      this.form.longitud = position.position.lng;
      this.form.unidad = position.name;
    },
    clickMaker: function(position) {
      console.log(position.position);
    },
    clickMapa: function(event) {
      console.log(event);
      this.marker.position.lat = event.latLng.lat();
      this.marker.position.lng = event.latLng.lng();
      this.form.latitud = event.latLng.lat();
      this.form.longitud = event.latLng.lng();
    },
    buscarToks: function() {
      const map = this.$refs.mapRef.$mapObject;
      const service = new google.maps.places.PlacesService(map);
      const request = {
        bounds: map.bounds,
        location: {
          lat: this.marker.position.lat,
          lng: this.marker.position.lng,
        },
        radius: 500000,
        type: "restaurant",
        keyword: "toks",
      };
      service.nearbySearch(request, (results, status) => {
        if (status == google.maps.places.PlacesServiceStatus.OK) {
          console.log(results);
          console.log(results[0].geometry.viewport);
          const a = this;
          var as = [{ fname: "foo" }];
          var bs = [{ lname: "bar" }];
          results.map((item) => {
            //a.setState({places: a.state.places.push({q: '2'})})
            const obj = {
              name: item.name,
              position: {
                lat: item.geometry.location.lat(),
                lng: item.geometry.location.lng(),
              },
            };
            a.markers.push(obj);
          });
        }
      });
    },
    getValidationClass(fieldName) {
      const field = this.$v.form[fieldName];

      if (field) {
        return {
          "md-invalid": field.$invalid && field.$dirty,
        };
      }
    },
    clearForm() {
      this.$v.$reset();
    },
    saveUnidad() {
      this.sedingInfo = true;
      let formData = new FormData();
      formData.append("token", localStorage.getItem("tokenUser_et"));
      formData.append("nombre", this.form.unidad);
      formData.append("latitud", this.form.latitud);
      formData.append("longitud", this.form.longitud);
      formData.append("id_marca", localStorage.getItem("id_marca_et"));
      formData.append("id_region", this.form.id_region);
      formData.append("id_estado", this.form.id_estado);
      formData.append("alcohol", this.form.alcohol);
      //alert(this.form.unidad);
      var a = this;
      const baseURI = this.$baseURL + "menudigital/index.php/sucursal/add_db";
      this.$http
        .post(baseURI, formData, { "Access-Control-Allow-Origin": "*" })
        .then((res) => {
          console.log(res);
          if (res.data.status != null) {
            if (res.data.status.toUpperCase() === "OK") {
              this.$router.push("/verUnidades");
            }

            if (res.data.status.toUpperCase() === "ERROR") {
              this.alertMessage = res.data.respuesta;
              this.showSnackbar = true;
              this.sedingInfo = false;
            }

            if (
              res.data.status.toUpperCase() != "OK" &&
              res.data.status.toUpperCase() != "ERROR"
            ) {
              this.alertMessage =
                "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
              this.showSnackbar = true;
              this.sedingInfo = false;
            }
          } else {
            this.alertMessage =
              "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
            this.showSnackbar = true;
            this.sedingInfo = false;
          }
          //
        })
        .catch(function(error) {
          // handle error
          console.log(error);
          a.alertMessage =
            "Ocurrio un error al intentar obtener la informacin del servidor, Comuniquese con soporte";
          a.showSnackbar = true;
          a.sedingInfo = false;
        });
    },
    validateUnidad() {
      this.$v.$touch();

      if (!this.$v.$invalid) {
        this.saveUnidad();
      }
    },
  },
  mounted() {
    const baseURI = this.$baseURL + "menudigital/index.php/sucursal/add";
    this.$http
      .get(
        baseURI,
        {
          params: {
            id_marca: localStorage.getItem("id_marca_et"),
            token: localStorage.getItem("tokenUser_et"),
          },
        },
        { "Access-Control-Allow-Origin": "*" }
      )
      .then((res) => {
        //this.users = result.data;
        console.log(res.data);
        if (res.data.status === "OK") {
          this.regiones = res.data.regiones;
          this.estados = res.data.estados;
          this.alcohol = res.data.alcohol;
          this.sedingInfo = false;
        }

        if (res.data.status.toUpperCase() === "ERROR") {
          this.alertMessage = res.data.respuesta;
          this.showSnackbar = true;
          this.sedingInfo = false;
          this.$router.push("/");
        }

        if (this.form.activo == 1) {
          this.boolean = true;
        } else {
          this.boolean = false;
        }
        //
      });
  },
};
</script>

